*{
  background-color: rgb(57, 55, 55);
}
.watch {
  width: 500px;
  height: 500px;
  border: 5px solid red;
  border-radius: 80%;
  position: relative;
  background-color: #ffff;
  margin: 50px auto;
}
.name {
top:18%;
left:36%;
color:red;
background-color:#ffff ;
z-index: 0;
font-size:130%;
font-weight: 500;
}
.point{
top:46%;
left:50%;
border-radius: 100%;
width:12px;
height:12px;
background-color:red;
}
.hand {
  position: absolute;
  transform-origin: 50% 100%;
  
  border-radius: 5px;
}

.second-hand {
  width: 2px;
  height: 180px;
  top: 10%;
  left: 51%;
  border-radius: 35%;
  background-color: red;
  transform: rotate(0deg);
}

.minute-hand {
  width: 9px;
  height: 180px;
  top: 11%;
  left: 51%;
  border-radius: 50%;
  background-color: rgb(11, 11, 11);
  transform: rotate(0deg);
}

.hour-hand {
  width: 10px;
  height: 120px;
  top: 23%;
  left: 50%;
  border-radius: 50%;
  background-color: rgb(11, 11, 11);
  transform: rotate(0deg);
}

.number {
  position: absolute;
  font-size: 1.3em;
  font-weight: bold;
  color: red;
  background-color: #ffff;
}

.number1 { top: 8%; left: 74%; }
.number2 { top: 25%; left: 90%; }
.number3 { top: 48%; left: 95%; }
.number4 { top: 67%; left: 90%; }
.number5 { top: 84%; left: 74.5%; }
.number6 { top: 92%; left: 50%; }
.number7 { top: 84%; left: 22.6%; }
.number8 { top: 67%; left: 7%; }
.number9 { top: 47%; left: 2%; }
.number10 { top: 25%; left: 7%; }
.number11 { top: 8%; left: 24%; }
.number12 { top: 1%; left: 47%; }